import Item from './item';
import { h } from '../element';
import { selectorMove } from '../sheet';
import { cssPrefix } from '../../config';

export default class Input extends Item {

  constructor(data) {
    super('input');
    this.tableData = data;
  }

  element() {
    const { tag } = this;
    this.vchange = () => {
      this.value = this.input.el.value;
      this.change(tag, this.input.el.value);
    };
    this.enter = (evt) => {
      const keyCode = evt.keyCode || evt.which;
      //input
      if (keyCode === 13) {
        evt.target.blur();
        //selectorMove(false, 'down');
        //evt.preventDefault();
      }
    }
    this.el = h('div', `${cssPrefix}-toolbar-btn`);
    this.input = h('input', '').css('width', '400px')
      .on('input', evt => this.vchange(evt))
      .on('keydown', evt => this.enter(evt))
      .attr('placeholder', '');
    this.el.child(this.input);
    return this.el;
  }

  setState(v) {
    if(v){
      this.input.el.value = v;
      this.value = v;
    }else{
      this.input.el.value = '';
      this.value = '';
    }
  }

}
